body.light-theme {
  background-color: var(--light-theme-color);
  color: var(--black-header-color);
}
body.dark-theme {
  background-color: var(--black-body-color);
  color: var(--white-color);
}
.toolbar {
  display: flex;
  justify-content: flex-end;
  width: 65px;
  padding: 10px 10px;
}

/**
  |============================
  | THEME SWITCH
  |============================
*/

.theme-switch__control {
  display: flex;
  align-items: center;
  flex-shrink: 0;
  position: relative;
  width: 40px;
  height: 20px;
  border-radius: 50%;
  padding: 0;
}

.theme-switch__control {
  display: flex;
  align-items: center;
  flex-shrink: 0;
  position: relative;
  width: 40px;
  height: 20px;
  border-radius: 50em;
  padding: 3px 0;
}
.theme-switch__toggle {
  position: absolute;
  left: 0;
  top: 0;
  margin: 0;
  padding: 0;
  opacity: 0;
  height: 0;
  width: 0;
  pointer-events: none;
}
.theme-switch__track {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  color: transparent;
  user-select: none;
  background: linear-gradient(#4f2ee8, #dcdcdc);
  box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1);
  border-radius: inherit;
  z-index: 1;
  cursor: pointer;
}
.theme-switch__marker {
  position: relative;
  background-color: #fff;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  z-index: 2;
  pointer-events: none;
  box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.1);
  transform: translateX(1px);
  transition: var(--transition-dur-and-func);
}
.theme-switch__toggle:checked + .theme-switch__track {
  background: linear-gradient(#4f2ee8, #686868);
  box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1);
}
.theme-switch__toggle:checked ~ .theme-switch__marker {
  transform: translateX(21px);
}
