body.show-modal .backdrop {
  opacity: 1;
  visibility: visible;
  pointer-events: initial;
}
body.show-modal .modal {
  transform: scale(1);
}

.backdrop {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 999;
  pointer-events: none;
  opacity: 0;
  visibility: hidden;
  transition: opacity 200ms ease-in-out, visibility 200ms ease-in-out;
}

.modal {
  padding: 24px;
  border-radius: 4px;
  max-width: 640px;
  width: 100%;
  min-height: 320px;
  background-color: #fff;
  transform: scale(1, 1);
  transition: transform 200ms ease-in-out;
}

.modal-img {
  width: 192px;
  height: 287px;

  border-radius: 8px;
}
.buy-links {
  display: flex;
  gap: 8px;
  align-items: center;
}

.amazon {
  width: 32px;
  height: 11px;
}

.apple,
.bookstore {
  width: 16px;
  height: 16px;
}

.buy-links {
  filter: grayscale();
}

.buy-links:hover,
.buy-links:focus {
  filter: grayscale(0) drop-shadow(0 3px 3px rgba(0, 0, 0, 0.15));
}
