h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

p:last-child {
  margin-bottom: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.header-page {
  width: 100%;
  z-index: 1000;
  background-color: #f6f6f6;
  border: 1.5px solid #111;
  border-radius: 0 0 8px 8px;
  position: fixed;
  top: 0;
  left: 0;
}

.header-container {
  text-align: center;
  height: 64px;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header-logo {
  padding-top: 18px;
  padding-bottom: 18px;
}

.header-nav {
  align-items: center;
  display: flex;
}

.header-nav-list {
  display: none;
}

.header-item {
  border-radius: 36px;
  padding: 8px 16px;
}

.header-item-link {
  align-items: center;
  display: flex;
}

.header-wrap {
  gap: 14px;
  display: flex;
}

.icon-shoping-list {
  fill: currentColor;
}

.user-authorization {
  width: 182px;
  background: var(--violet-color);
  border-radius: 36px;
  justify-content: space-between;
  align-items: center;
  gap: 9px;
  padding: 4px 12px;
  display: none;
}

.user-authorization-title {
  color: var(--white-color);
  letter-spacing: -.44px;
  font-size: 22px;
  font-weight: 700;
  line-height: normal;
}

.mobile-icon-hidden {
  display: none;
}

.mobile-button-menu {
  min-height: 28px;
  min-width: 28px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  outline: none;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
}

@media (min-width: 768px) {
  .mobile-button-menu, .menu-togle {
    display: none;
  }
}

.menu-container {
  width: 100%;
  height: 100vh;
  z-index: 999;
  background: radial-gradient(189.32% 190.93% at 59.76% -23.42%, #4f2ee8 18.03%, #fff 100%);
  padding: 84px 20px 20px;
  transition: transform .25s ease-in-out;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(100%);
}

.menu-container.is-open {
  transform: translateX(0);
}

.menu-container .mobile-button-menu {
  color: #000;
  position: absolute;
  top: 16px;
  right: 16px;
}

.mobile-menu {
  margin: 0;
  padding: 0;
  list-style: none;
}

.mobile-menu .link {
  color: #fff;
  padding: 10px;
  text-decoration: none;
  display: block;
}

.mobile-button-menu.is-open .mobile-button-menu-icon-close-menu, .mobile-button-menu-icon-togle {
  stroke: var(--black-body-color);
  display: block;
}

.mobile-button-menu-icon-togle:hover, .mobile-button-menu-icon-togle:focus {
  stroke: var(--violet-color);
}

.menu-user-login {
  align-items: center;
  gap: 9px;
  margin-bottom: 126px;
  display: flex;
}

.menu-user-login-name {
  color: #fff;
  letter-spacing: -.44px;
  font-size: 22px;
  font-weight: 700;
  line-height: normal;
}

.mobile-menu-nav-list {
  color: #fff;
  letter-spacing: -.56px;
  text-transform: uppercase;
  flex-direction: column;
  gap: 16px;
  font-weight: 500;
  line-height: 1.28;
  display: flex;
}

.mobile-menu-btn-down {
  border: 1.5px solid var(--black-body-color);
  background: var(--white-color);
  border-radius: 18px;
  padding: 15px 20px;
  position: fixed;
  bottom: 20px;
  left: 20px;
}

.mobile-menu-btn-down-icon {
  stroke: var(--yellow-color);
}

.mobile-menu-btn-down-link {
  align-items: center;
  display: flex;
}

.mobile-menu-btn-down-item {
  letter-spacing: -.56px;
  margin-right: 4px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.28;
}

@media screen and (min-width: 768px) {
  .header-container {
    height: 72px;
  }

  .header-logo {
    margin-right: 40px;
    padding-top: 22px;
    padding-bottom: 22px;
  }

  .header-nav-list {
    letter-spacing: -.56px;
    text-transform: uppercase;
    gap: 24px;
    font-weight: 700;
    line-height: 1.28;
    display: flex;
  }

  .header-item {
    border-radius: 36px;
    padding: 8px 16px;
  }

  .active-link {
    background-color: #eac645;
  }

  .header-item-link-shoping-list {
    margin-right: 8px;
  }

  .user-authorization {
    display: flex;
  }
}

@media screen and (min-width: 1440px) {
  .header-logo {
    margin-right: 72px;
  }
}

body.dark-theme .header-page {
  background-color: var(--black-header-color);
  border: 1.5px solid var(--white-color);
}

body.dark-theme .mobile-button-menu-icon-togle {
  stroke: var(--white-color);
}

.categories {
  width: 335px;
  max-height: 228px;
  scrollbar-width: 8px;
  scrollbar-color: var(--grey-categories-color);
  margin: 0 auto;
  overflow-x: hidden;
  overflow-y: scroll;
}

.category-item {
  color: rgba(17, 17, 17, .6);
  letter-spacing: -.02em;
  cursor: pointer;
  font-size: 16px;
  line-height: 1.13;
}

.category-item:hover, .category-item:active, .category-item:focus {
  transition: transform .35s ease-in;
  transform: scale(.99);
}

ul .category-item:not(:first-child) {
  padding-top: 24px;
}

.categories::-webkit-scrollbar {
  width: 8px;
}

.categories::-webkit-scrollbar-track {
  background: none;
}

.categories::-webkit-scrollbar-thumb {
  background-color: var(--grey-categories-color);
  border: none;
  border-radius: 20px;
}

@media screen and (min-width: 768px) {
  .categories {
    min-width: 337px;
    max-height: 472px;
    margin: 0;
  }

  .category-item {
    color: rgba(17, 17, 17, .6);
    font-size: 18px;
    line-height: 1.33;
  }

  .category-item:not(:first-child) {
    padding-top: 32px;
  }
}

@media screen and (min-width: 1440px) {
  .categories {
    width: 356px;
    margin: 0;
  }

  .category-item:active, .category-item:focus {
    transform: none;
  }
}

.is-chosen {
  text-transform: uppercase;
  color: var(--violet-color);
  transition: color var(--transition-dur-and-func);
  font-weight: 700;
}

body.dark-theme .categories {
  scrollbar-color: var(--grey-categories-color);
}

body.dark-theme .category-item {
  color: rgba(255, 255, 255, .6);
}

body.dark-theme .is-chosen {
  color: var(--yellow-color);
}

body.dark-theme .categories::-webkit-scrollbar-thumb {
  background-color: rgba(17, 17, 17, .6);
}

