@media screen and (min-width: 768px) {
  .one-category-container {
    max-width: 768px;
  }

  .one-category-gallery {
    width: 704px;
    flex-flow: wrap;
    gap: 25px;
    display: flex;
  }

  .category-title {
    font-size: 48px;
    line-height: 52px;
  }

  .one-category-item {
    width: calc(33.3333% - 16.6667px);
