:root {
  --black-header-color: #111;
  --black-body-color: #202024;
  --violet-color: #4f2ee8;
  --white-color: #fff;
  --grey-text-color: #b4afaf;
  --yellow-color: #eac645;
  --light-theme-color: #f6f6f6;
  --grey-categories-color: #e0e0e0;
  --transition-dur-and-func: 250ms cubic-bezier(.4, 0, .2, 1);
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

body {
  color: var(--black-header-color);
  letter-spacing: -.04em;
  background-color: #f6f6f6;
  font-family: DM Sans, sans-serif;
  font-size: 14px;
  font-weight: 400;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

