#btntotop {
  z-index: 77;
  background-color: var(--violet-color);
  color: #fff;
  cursor: pointer;
  border: none;
  border-radius: 50%;
  outline: 5px solid rgba(79, 46, 232, .3);
  padding: 15px;
  transition: all .35s linear;
  display: none;
  position: fixed;
  bottom: 54px;
  right: 54px;
}

.btntotoparrow {
  fill: currentColor;
  transition: all .35s linear;
}

@media screen and (min-width: 1000px) {
  #btntotop:hover, #btntotop:focus {
    color: var(--violet-color);
    outline-color: var(--violet-color);
    background-color: rgba(79, 46, 232, .3);
  }

  #btntotop:hover .btntotoparrow, #btntotop:focus .btntotoparrow {
    transform: rotate(360deg)scale3d(1.5, 1.5, 1);
  }
}

body.show-modal .backdrop {
  opacity: 1;
  visibility: visible;
  pointer-events: initial;
}

body.show-modal .modal {
  transform: scale(1);
}

.backdrop {
  width: 100vw;
  height: 100vh;
  z-index: 999;
  pointer-events: none;
  opacity: 0;
  visibility: hidden;
  background-color: rgba(0, 0, 0, .3);
  justify-content: center;
  align-items: center;
  transition: opacity .2s ease-in-out, visibility .2s ease-in-out;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.modal {
  max-width: 640px;
  width: 100%;
  min-height: 320px;
  background-color: #fff;
  border-radius: 4px;
  padding: 24px;
  transition: transform .2s ease-in-out;
  transform: scale(1);
}

.modal-img {
